@import '../../../assets/scss/variables.scss';

.wrapper {
    position: relative;
    & .dropdown-window {
        padding: 10px 0;
        border-radius: 15px;
        box-shadow: 0 0 20px 0px rgba(41, 41, 41, 0.2);
        min-width: 240px;
        position: absolute;
        right: 0;
        margin-top: 0.5rem;
        background-color: $light;
        z-index: 999;
        & ul {
            list-style: none;
            & li {
                padding: 10px 15px;
                cursor: pointer;
            }
            &.border-top {
                border-top: 1px solid rgba($grey, 0.15);
                margin-top: 0.5rem;
            }
        }
    }
}